import { Injectable } from '@angular/core';
import { IGuest } from "@shared/models/guest.model";
import { IReservation } from "@shared/models/reservation.model";

@Injectable({
  providedIn: 'root'
})
export class GuestService {

  private _guestData: IGuest = {} as IGuest;
  private _guestReservationData: IReservation = {} as IReservation;

  constructor() {}

  set guestData(data) {
    this._guestData = data;
  }

  get guestData() {
    return this._guestData;
  }

  set guestReservationData(data) {
    this._guestReservationData = data;
  }

  get guestReservationData() {
    return this._guestReservationData;
  }
}
