import { Injectable } from '@angular/core';
import { ISelection, ISelectionDto } from "@shared/models/selection.model";

@Injectable({
  providedIn: 'root'
})
export class SelectionService {

  private _selectionDto: ISelectionDto;
  private _selectionPages: Array<ISelection>;

  constructor() {}

  set selectionDto({selectionName, selectionOption}: ISelectionDto) {
    if (!this._selectionDto) {
      this._selectionDto = {};
    }
    this._selectionDto[selectionName] = selectionOption;
  }

  get selectionDto() {
    if (!this._selectionDto) {
      return {};
    }
    return this._selectionDto;
  }

  set selectionPages(val: Array<ISelection>) {
    this._selectionPages = val;
  }

  get selectionPages() {
    return this._selectionPages;
  }
}
