import { Injectable } from '@angular/core';
import { IHotel } from "@shared/models/hotel.model";

@Injectable({
  providedIn: 'root'
})
export class HotelService {

  private _hotelData: IHotel = {} as IHotel;

  constructor() {}

  set hotelData(data) {
    this._hotelData = data;
  }

  get hotelData() {
    return this._hotelData;
  }
}
