import { Component, Input, OnInit } from '@angular/core';
import { IRoom } from "@shared/models/room.model";
import { IGuest } from "@shared/models/guest.model";
import { CoreRoutes } from "@core/configs/routing-config";
import { RoomsMapService } from "@core/services/rooms-map-service/rooms-map.service";
import { Router } from "@angular/router";
import { IReservation } from "@shared/models/reservation.model";

@Component({
  selector: 'app-confirm-modal',
  templateUrl: './confirm-modal.component.html',
  styleUrls: ['./confirm-modal.component.scss']
})
export class ConfirmModalComponent implements OnInit {

  @Input() isVisible: boolean = false;
  @Input() selectedRoom: IRoom = {} as IRoom;
  @Input() guestData: IGuest = {} as IGuest;
  @Input() reservationData: IReservation = {} as IReservation;

  constructor(
      private router: Router,
      private _roomsMapService: RoomsMapService,
  ) { }

  ngOnInit(): void {}

  closeConfirmDialog(): void {
    this._roomsMapService.isShowConfirmDialog$.next(false);
  }

  confirmOrder(): void {
    this._roomsMapService.confirmedOrderRoom = this.selectedRoom.roomNumber;
    this.closeConfirmDialog();
    this.router.navigate(['/' + CoreRoutes.COMPLETED], { queryParams: { 'room': this.selectedRoom.roomNumber } });
  }
}
