import { Component, Input, OnInit } from '@angular/core';
import { UtilityService } from "@core/utilities/utility.service";

@Component({
  selector: 'app-images-pre-render',
  templateUrl: './images-pre-render.component.html',
  styleUrls: ['./images-pre-render.component.scss']
})
export class ImagesPreRenderComponent implements OnInit {

  @Input() set data(val: Object) {
    if (!this._utilityService.isObjectEmpty(val)) {
      this.setImagesListToRender(val);
    }
    this._data = val;
  };

  get data() {
    return this._data;
  }

  readonly regExp = /"(?:src|[\w_]*logo)":("[^\s"]+")/gm;

  public imagesListToRender: Array<string> = [];
  private _data: Object;

  constructor(private _utilityService: UtilityService) {}

  ngOnInit(): void {}

  setImagesListToRender(data): void {
    let pagesDataJson = JSON.stringify(data);
    let imagesList = pagesDataJson.match(this.regExp);

    this.imagesListToRender = imagesList.map(image => {
      let symbols = image.split('"');

      return symbols[symbols.length - 2];
    });
  }
}
