import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'dateView'
})
export class DateViewPipe implements PipeTransform {

  transform(value: string, ...args: unknown[]): string {
    if (!value) {
      return '';
    }
    const [weekday, month] = value.split(',');
    const [,,monthday] = month.split(' ');

    return `${weekday.slice(0,3)},${month.slice(0,4)} ${monthday}`;
  }
}
