import { Injectable } from '@angular/core';
import { IPagesData } from "@shared/models/pages-data.model";
import { HttpClient } from "@angular/common/http";
import { Observable, throwError } from "rxjs";
import { environment } from "../../../../environments/environment";
import { catchError } from "rxjs/operators";
import { Router } from "@angular/router";
import { CoreRoutes } from "@core/configs/routing-config";

@Injectable({
  providedIn: 'root'
})
export class DataService {

  private _pagesData: IPagesData;

  constructor(
      private http: HttpClient,
      private router: Router,
  ) {}

  set pagesData(pagesData: IPagesData) {
    this._pagesData = pagesData;
  }

  get pagesData(): IPagesData {
    return this._pagesData;
  }

  fetchPagesData(link: string): Observable<IPagesData> {
    return this.http.get<IPagesData>(environment.API + '/message/' + link)
      .pipe(
        catchError(err => {
          this.router.navigate([CoreRoutes.LINK_NOT_VALID]);

          return throwError(err);
        })
      )
  }

  // fetchPagesData(link: string): Observable<IPagesData> {
  //   return of(PAGES_DATA);
  // }
}
