import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, throwError } from "rxjs";
import { environment } from "../../../../environments/environment";
import { catchError } from "rxjs/operators";
import { HttpClient } from "@angular/common/http";
import { IRoom } from "@shared/models/room.model";

@Injectable({
  providedIn: 'root'
})
export class RoomsMapService {

  isShowConfirmDialog$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  isShowMobileMapDialog$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  confirmedOrderRoom: number = 0;

  constructor(
      private http: HttpClient
  ) {}

  getAllRoomsById(hotelId: number): Observable<Array<IRoom>> {
    return this.http.get<Array<IRoom>>(environment.API + '/room/' + hotelId)
      .pipe(
        catchError(err => {
          console.log(err);
          return throwError(err);
        })
      )
  }
}
