import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot, CanActivate,
  CanActivateChild,
  Router,
  RouterStateSnapshot,
  UrlTree
} from '@angular/router';
import { Observable } from 'rxjs';
import { LocalStorageService } from "../../services/store-service/store.service";

@Injectable({
  providedIn: 'root'
})
export class SelectionGuard implements CanActivateChild, CanActivate {

  constructor(
      private _storeService: LocalStorageService,
      private router: Router
  ) {}

  canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return this.isPagesDataExist();
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return this.isPagesDataExist();
  }

  isPagesDataExist(): boolean {
    if (this._storeService.getItem('hash')) {
      return true;
    }
    this.router.navigate(['/'],{ queryParams: { hash: this._storeService.getItem('hash') }});
  }
}
